import { onMounted, ref } from 'vue';
export default {
  name: 'ModelsCell',

  setup() {
    const items = [{
      title: '卡券中心',
      icon: require('@/assets/user_side_img/ico_功能区_优惠券@2x.webp')
    }, {
      title: '账户充值',
      icon: require('@/assets/user_side_img/ico_功能区_账户充值@2x.webp')
    }, {
      title: '待收货',
      icon: require('@/assets/user_side_img/ico_功能区_待收货@2x.webp')
    }, {
      title: '我的任务',
      icon: require('@/assets/user_side_img/ico_功能区_我的任务@2x.webp')
    }, {
      title: '我的收藏',
      icon: require('@/assets/user_side_img/ico_功能区_收藏夹@2x.webp')
    }];
    const containerView = ref(null);
    const slider = ref(null);
    onMounted(() => {
      const clientWidth = containerView.value.clientWidth;
      const w = clientWidth / 5 * items.length;
      containerView.value.style.width = w + 'px';
      console.log(containerView);
    });

    const scrollEvent = e => {
      const scrollView = e.target;
      const proportion = scrollView.scrollLeft / scrollView.scrollWidth;
      const sliderView = slider.value;
      const offsetWidth = sliderView.parentElement.offsetWidth * proportion;
      sliderView.style.transform = `translateX(${offsetWidth}px)`;
    };

    return {
      items,
      containerView,
      scrollEvent,
      slider
    };
  }

};