import { computed } from 'vue';
export default {
  name: 'GoodsCell',
  props: ['goods'],

  setup(props) {
    const activityImg = computed(() => {
      if (props.goods.activityType === 1) {
        return require('@/assets/user_side_img/标签_活动限时特价@2x.webp');
      } else if (props.goods.activityType === 2) {
        return require('@/assets/user_side_img/标签_活动下单返券@2x.webp');
      } else if (props.goods.activityType === 3) {
        return require('@/assets/user_side_img/标签_活动满件减@2x.webp');
      } else if (props.goods.activityType === 4) {
        return require('@/assets/user_side_img/标签_活动优惠券@2x.webp');
      }

      return require('@/assets/user_side_img/标签_活动优惠券@2x.webp');
    });

    const buttonTitle = () => {
      if (props.goods.buyState === 1) {
        return '即将开售';
      } else if (props.goods.buyState === 3) {
        return '用券购买';
      } else if (props.goods.buyState === 4) {
        return '去获券';
      }

      return '';
    };

    const isPreferential = (props.goods.preferential || '').length !== 0;
    return {
      activityImg,
      isPreferential,
      buttonTitle
    };
  }

};