import { onMounted, ref } from 'vue';
import { debounce } from '@/common/utility';
export default {
  name: 'IconsCell',

  setup() {
    const items = [{
      title: '零售挂机',
      icon: require('@/assets/user_side_img/ico_金刚区_零售挂机图片@2x.webp')
    }, {
      title: '零售柜机',
      icon: require('@/assets/user_side_img/ico_金刚区_零售柜机图片@2x.webp')
    }, {
      title: '商用风管',
      icon: require('@/assets/user_side_img/ico_金刚区_商用风管图片@2x.webp')
    }, {
      title: '环境电器',
      icon: require('@/assets/user_side_img/ico_金刚区_环境电器图片@2x.webp')
    }, {
      title: '冰箱',
      icon: require('@/assets/user_side_img/ico_金刚区_冰箱图片@2x.webp')
    }, {
      title: '热水器',
      icon: require('@/assets/user_side_img/ico_金刚区_热水器图片@2x.webp')
    }, {
      title: '智能家居',
      icon: require('@/assets/user_side_img/ico_金刚区_智能家居图片@2x.webp')
    }, {
      title: '厨房大电',
      icon: require('@/assets/user_side_img/ico_金刚区_厨房大电图片@2x.webp')
    }, {
      title: '厨房小电',
      icon: require('@/assets/user_side_img/ico_金刚区_厨房小电图片@2x.webp')
    }, {
      title: '个护产品',
      icon: require('@/assets/user_side_img/ico_金刚区_个护产品图片@2x.webp')
    }, {
      title: '洗衣机',
      icon: require('@/assets/user_side_img/ico_金刚区_洗衣机图片@2x.webp')
    }, {
      title: '电风扇',
      icon: require('@/assets/user_side_img/ico_金刚区_电风扇图片@2x.webp')
    }];
    const containerView = ref(null);
    const slider = ref(null);
    onMounted(() => {
      const clientWidth = containerView.value.clientWidth;
      const w = clientWidth / 5 * items.length;
      containerView.value.style.width = w + 'px';
      console.log(containerView);
    });

    const scrollEvent = e => {
      debounce(function () {
        const scrollView = e.target;
        const proportion = scrollView.scrollLeft / scrollView.scrollWidth;
        const sliderView = slider.value;
        const offsetWidth = sliderView.parentElement.offsetWidth * proportion;
        sliderView.style.transform = `translateX(${offsetWidth}px)`;
      }, 100)();
    };

    return {
      items,
      containerView,
      scrollEvent,
      slider
    };
  }

};