import Frame from '@/common/frame'

export default class WaterLayout {
  constructor (container) {
    this.container = container
  }

  layout () {
    const jianJv1 = 8 // px
    const jianJv2 = 12 // px
    const offsetWidth = this.container.offsetWidth
    const width = (offsetWidth - 8) / 2
    const children = this.container.children
    const array = [0, 0]
    for (let i = 0; i < children.length; i++) {
      const view = children[i]
      view.style.width = width + 'px'
      const frame = new Frame(view)
      if (array[0] > array[1]) {
        frame.left = width + jianJv1
        frame.top = array[1]
        array[1] = frame.bottom + jianJv2
      } else {
        frame.left = 0
        frame.top = array[0]
        array[0] = frame.bottom + jianJv2
      }
      frame.updateFrame()
    }
    const max = Math.max(...array)
    this.container.style.height = max + 'px'
  }
}
